import React from 'react';
import Button from '../Components/Button/Button';

// Это объект с мета-информацией для Storybook
export default {
    title: 'Components/Button', // Название в панели Storybook
    component: Button,          // Компонент, который мы показываем
    argTypes: {                 // Настройки для контролов
        variant: {
            control: { type: 'radio' },
            options: ['text', 'contained', 'outlined'],
        },
        size: {
            control: { type: 'radio' },
            options: ['small', 'medium', 'large'],
        },
        disabled: { control: 'boolean' },
    },
};

// "Шаблон" — функция, которая рендерит компонент с аргументами
const Template = (args) => <Button {...args} />;

// Примеры (stories) кнопок
export const TextButton = Template.bind({});
TextButton.args = {
    variant: 'text',
    size: 'small',
    disabled: false,
};

export const ContainedButton = Template.bind({});
ContainedButton.args = {
    variant: 'contained',
    size: 'medium',
    disabled: false,
};

export const OutlinedButton = Template.bind({});
OutlinedButton.args = {
    variant: 'outlined',
    size: 'large',
    disabled: false,
};
